import {useSearchParams} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {useEffect, useState} from "react";
import {useForm} from "react-hook-form";

import {locationAction} from "../../../redux";
import {formFilterLocationHelper} from "../../../helpers";
import {Location} from "../Location/Location";

const Locations = () => {
    const [query,] = useSearchParams();
    const dispatch = useDispatch();
    const [filter, setFilter] = useState({});
    const {locations, count, limit, page} = useSelector(state => state.locationReducer);

    useEffect(() => {
        dispatch(locationAction.getAll(filter))
    }, [filter]);

    const {handleSubmit, register, reset, formState: {isValid, errors}} = useForm();

    const submit = (formFilter) => {
        const formFilterHelper = formFilterLocationHelper(formFilter);

        if (Object.keys(formFilterHelper).length !== 0) {
            setFilter(formFilterHelper)
        }
    };
    const resetFilter = () => {
        reset()
        setFilter({})
    };

    const nextPage = () => {
        if (page * limit < count) {
            setFilter({...filter, page: page + 1})
        }
    }
    const prevPage = () => {
        if (page > 1) {
            setFilter({...filter, page: page - 1})
        }
    }

    return (
        <div>
            <form onSubmit={handleSubmit(submit)}>
                <input type="number" placeholder={'page'} {...register('page')}/> <label></label> <br/>
                <input type="number" placeholder={'limit'} {...register('limit')}/> <label></label> <br/>
                <select {...register('status')}>
                    <option value="" disabled selected hidden></option>
                    <option value=''></option>
                    <option value=''></option>
                    <option value=''></option>
                    <option value=''></option>
                </select>
                <label></label> <br/>
                <select {...register('region')}>
                    <option value="" disabled selected hidden></option>
                    <option value=''></option>
                    <option value=''></option>
                    <option value=''></option>
                    <option value=''></option>
                    <option value=''></option>
                </select>
                <label></label> <br/>
                <input type="text" placeholder={'city'} {...register('city')}/> <label></label> <br/>
                <input type="text" placeholder={'address'} {...register('address')}/> <label></label> <br/>
                <input type="text" placeholder={'phone'} {...register('phone')}/> <label></label> <br/>
                <button>filter</button>
            </form>
            <div>
                <button onClick={resetFilter}>reset filter</button>
            </div>

            <div>
                  {count} <br/>
                  {limit} <br/>
                 {page}
            </div>


            <table>
                <thead>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                </thead>
                <tbody>
                {locations.map(location => <Location key={location._id} location={location}/>)}
                </tbody>
            </table>


            <div>
                <button onClick={prevPage}></button>
                <button onClick={nextPage}></button>

            </div>
        </div>
    );
};
export {Locations};

